<template>
<div id="wrapper">
<div id="detail__page">
  <div class="detail-pic" :style="{backgroundImage : `url(${productInfo.image})`}"></div>
  <!-- <div class="detail__text">
    <h4>작성자 : {{ productInfo.name }}</h4>
    <hr>
    <h4 class="title">{{ productInfo.title }}</h4>
    <p class="date">{{ productInfo.date }}</p>
    <p class="price">가격 : {{ productInfo.price }} 원</p>
    <div>내용 : {{ productInfo.detail }}</div>
  </div>
  <div class="detail__buttons">
    <button :disabled="btnEdit">수정</button>
    <button :disabled="btnDel">삭제</button>
    <button :disabled="btnChat">채팅문의</button>
  </div> -->
  <div class="product__text">
    <h2 class="product__title">{{ productInfo.title }}</h2>
    <table>
      <colgroup>
        <col style="width:173px">
        <col>
      </colgroup>
      <tbody>
        <tr>
          <th>상품코드</th>
          <td>{{ productCode }}</td>
        </tr>
        <tr>
          <th>판매자</th>
          <td>{{ productInfo.name }}</td>
        </tr>
        <tr>
          <th>구매수량</th>
          <td>
            <input class="buyCnt" type="number" min="1" value="1"> 개
          </td>
        </tr>
        <tr>
          <th>배송비</th>
          <td>무료배송</td>
        </tr>
        <tr>
          <th>판매가</th>
          <td class="price"><b>{{ productInfo.price }}</b>원</td>
        </tr>
      </tbody>
    </table>

<div class="detail__buttons">
  <div class="detail__buttons__editDel">
    <button v-if="btnEdit == false" :disabled="btnEdit" class="btnEdit">수정</button>
    <button v-if="btnDel == false" :disabled="btnDel" class="btnDel">삭제</button>
  </div>

  <div class="detail__buttons__chatBuy">
    <button :disabled="btnChat" class="btnChat">채팅문의</button>
    <button :disabled="btnBuy" class="btnBuy">장바구니</button> 
  </div>
</div>


  </div>
</div>

</div>
</template>

<script>
export default {
  name : 'DetailPage',
  data() {
    return {
      btnEdit : true,
      btnDel : true,
      btnChat : true,
      btnBuy : true,
      paramId : '',
      db : this.$store.state.db,
      loginUserInfo : localStorage.getItem('user'),
      productInfo : '',
      productCode : '',
    }
  },
  beforeMount() {
    var querystring = new URLSearchParams(window.location.search);
    this.paramId = querystring.get('id');
    
    this.db.collection('product').doc(this.paramId).get()
    .then((result)=>{
      var data = result.data();
      data.date = data.date.toDate().toLocaleString();  //날짜 형식 변경

      if(data.image == ''){ //이미지가 없는경우 기본이미지 설정
        data.image = 'https://firebasestorage.googleapis.com/v0/b/vue-project01-a89a2.appspot.com/o/image%2Fvecteezy_icon-image-not-found-vector_.jpg?alt=media&token=c01fbe73-5dde-426c-a2c0-e48962987df6';
      }

      this.productInfo = data;
      this.productCode = result.id;

      if(this.loginUserInfo != null){
        //버튼 권한 설정
        if(JSON.parse(this.loginUserInfo).uid == result.data().uid){  //자신의게시물
          this.btnChat = true;
          this.btnDel = false;
          this.btnEdit = false;
          this.btnBuy = true;
        }else if(JSON.parse(this.loginUserInfo).email == 'admin@admin.com'){  //관리자
          this.btnChat = false;
          this.btnDel = false;
          this.btnEdit = false;
          this.btnBuy = false;
        }else{  //일반사용자
          this.btnChat = false;
          this.btnDel = true;
          this.btnEdit = true;
          this.btnBuy = false;
        }
      }

    })
  },
}
</script>

<style scoped>
  :root{
    --text-color:#f0f4f5;
    --background-color : #263343;
    --accent-color : #d49466;
  }
  #wrapper{
    display: flex;
    justify-content: center;
    
  }
  #detail__page{
    width: 700px;
    padding: 50px 10px 20px 10px;
    max-width: 700px;
    background:var(--text-color)
  }
  .detail-pic {
  width: 100%;
  height: 300px;
  background-size: cover;
  background-position: center;
  } 
  /* .detail__text{
    padding:10px;
  }
  .textarea_field {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 4px;
    margin: 3px 0;
    font-size: 16px;
    width: 650px;
    height: 160px;
    font-family: 'NanumBarunGothic';
}

.detail__buttons{
  margin : 10px;
} */

@media screen and (max-width: 768px) {
  #detail__page{
  max-width: 100%;
  padding-left: 10px;
  padding-right: 10px;

  }
}

.product__text .product__title{
  font-size: 24px;
  padding: 0 0 20px;
  border-bottom: 2px solid var(--background-color);
  line-height: 26px;
}

.product__text .price{
  font-size:19px;
  color: blue;
}
.product__text .price b{ 
    font-size:22px;
    color: blue;
}
.product__text table th,
.product__text table td{
    padding:14px;
    font-size:15px;
    color: var(--background-color);
    text-align: left;
}

.product__text table .buyCnt{
    position: relative;
    border: 1px solid var(--background-color);
    width: 40px;
    height: 30px;
    text-align: center;
    font-size: 16px;
}
.detail__buttons__editDel{
  display: flex;
  justify-content: flex-start;
  position: relative;
}
.detail__buttons__chatBuy{
  display: flex;
  justify-content: flex-end;
  position: relative;
}
.detail__buttons{
  justify-content: space-between;
  display: flex;
}

.detail__buttons__editDel .btnEdit,
.detail__buttons__editDel .btnDel{
  background-color: var(--background-color);
  border:none;
  color:var(--text-color);
  border-radius: 5px;
  font-size : 14px;
  padding: 5px 10px 5px 10px;
  margin-right: 5px;
  cursor: pointer;
}

.detail__buttons__chatBuy .btnChat,
.detail__buttons__chatBuy .btnBuy{
  background-color: var(--background-color);
  border:none;
  color:var(--text-color);
  border-radius: 5px;
  font-size : 14px;
  padding: 5px 10px 5px 10px;
  margin-right: 5px;
  cursor: pointer;
}

.detail__buttons__chatBuy button:disabled,
.detail__buttons__editDel button:disabled{
  background: var(--text-color);
  cursor: default;
}

</style>